import React,{useState,useContext} from 'react'; import { AuthContext } from '../contexts/AuthContext'; export default function Login(){ const { login } = useContext(AuthContext); const [email,setEmail]=useState(''); const [password,setPassword]=useState(''); const submit=async e=>{ e.preventDefault(); await login(email,password); window.location.href='/vitclass'; }; return (<div className='center-container'><h2>Sign in</h2><form onSubmit={submit} className='card'><input value={email} onChange={e=>setEmail(e.target.value)} placeholder='email' className='w-full p-2 border rounded' /><input type='password' value={password} onChange={e=>setPassword(e.target.value)} placeholder='password' className='w-full p-2 border rounded' /><div style={{marginTop:8}}><button className='btn btn-primary' type='submit'>Sign in</button></div></form></div>) }
